.shopping-list {
	width: 100%;
	overflow: hidden;
}

.plant-list {
	list-style-type: none;
	padding: 32px;
	position: relative;
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	align-items: center;
}
#swiper-container {
	width: 50%;
	border: 0;
	
}
.carrousselImg {
	width: 100%;
	height: 63vh;
}
.swiper-wrapper {
	padding-inline-start: 0;
}

@media screen and (max-width: 650px) {
	#swiper-container {
		width: 100%;
	}
}
.plant-info-container {
	position: relative;
	margin-bottom: 35px;
}
.add-article-button {
	margin-top: 0;
	position: absolute;
	left: 0;
	right: 0;
	margin: auto;
}
