.cart {
	color: white;
	background-color: #cc071d;
	padding: 12px;
	display: flex;
	flex-direction: column;
	justify-content: flex-start;
	width: 120px;
}

.cart-add-button {
	margin-left: 25px;
	cursor: pointer;
}

.cart-toggle-button {
	background: none;
	outline: none;
	border: none;
	font-weight: 600;
	cursor: pointer;
	align-self: self-start;
	padding: 0;
	color: white;
}

.cart-closed {
	padding: 10px;
	width: 100px;
}

.cart-closed > button {
	color: black;
}

.articles {
	padding-left: 12px;
}
.vider-panier {
	background: white;
	border: 0;
	color: black;
	font-weight: normal;
}


