.plant-item-cover {
	height: 250px;
	width: 250px;
	border-radius: 20px;
}

.plant-item {
	margin: 10px;
	display: flex;
	align-items: flex-start;
	justify-content: center;
	flex-direction: column;
	text-transform: capitalize;
	position: relative;
}

.plant-item-price {
	position: absolute;
	border-radius: 20px 20px 20px 0;
	background-color: #d10921f6;
	color: white;
	padding: 8px;
	right: 0;
	z-index: 5;
	top: 0;
	font-weight: 500;
}
#item-gallery-swiper {
	height: 250px;
	width: 250px;
}